<template>
    <el-container>
        <el-header>
            <ul class="menu" :style="{width:headWidth}">
                <li v-for="(tab,index) in tabs" v-bind:key="index" class="menuItem"> 
                    <router-link :to="tab.id">{{tab.name}}</router-link>
                </li>
            </ul>
        </el-header>
        <el-main>
            <router-view></router-view>
        </el-main>
    </el-container>
</template>
<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      headWidth: null,
      tabs:[
        {
          id:"Note",
          name:"关注"
        },
        {
          id:"Note",
          name:"演唱会"
        },
        {
          id:"Note",
          name:"科技"
        },
        {
          id:"Note",
          name:"动漫"
        },
        {
          id:"Note",
          name:"车展"
        },
        {
          id:"Note",
          name:"财经"
        },
        {
          id:"Note",
          name:"娱乐"
        },
        {
          id:"Note",
          name:"科技"
        },
        {
          id:"Note",
          name:"财经"
        },
      ]
    }
  },
  mounted(){
    this.headWidth = this.tabs.length * 4 + "em";
  }
}
</script>
<style scoped> 
.el-header{
    height: 3em !important;
    background-color: #FEFEFE;  
    padding: 0% !important;
    overflow-y: hidden;
}
.el-main{
  padding: 0em;
  margin: 0em;
}
.menu{
  margin: 0px;
  padding: 0px; 
}
.menuItem{
  margin: 0px;
  padding: 0px;
  width: 4em;
  float: left;
  text-align: center;
  list-style-type: none;
  line-height: 3em; 
}
.menuItem a{
  text-decoration: none;
  color: #858585;
}
</style>


